      :network-name="network.network_name"
      :edited-item="editedItem"
      :on-check-existing="isExistingvalue"
    ></bm-device-form-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn
              x-small
              class="mr-1 btn-height-22 white--text"
              color="primary"
              @click="clickContinue()"
              :disabled="!dataImport"
            >
              Continue
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="validWizardform" ref="form_wizard" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="device_uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
                :loading="savingImport"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    />
                  </v-toolbar>
                </template>

                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> mdi-delete </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item, 'device_uuid')"
                    >mdi-check-circle-outline</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="createDevice(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item, 'device_uuid')"
                    >mdi-cached</v-icon
                  >
                </template>
                <template v-slot:[`item.device_name`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'device_uuid')"
                    v-model="item.device_name"
                    single-line
                    style="width: 150px"
                    disabled
                    dense
                  />
                  <v-text-field
                    v-else
                    v-model="item.device_name"
                    single-line
                    style="width: 150px"
                    required
                    :rules="[() => !isExistingvalue(item, 'device_name') || 'Name Existed', rules.required]"
                    dense
                  />
                </template>
                <template v-slot:[`item.device_enable`]="{ item }">
                  <v-checkbox v-model="item.device_enable" dense></v-checkbox>
                </template>
                <template v-slot:[`item.type_mstp`]="{ item }">
                  <v-checkbox v-model="item.type_mstp" dense></v-checkbox>
                </template>
                <template v-slot:[`item.supports_rpm`]="{ item }">
                  <v-checkbox v-model="item.supports_rpm" dense></v-checkbox>
                </template>
                <template v-slot:[`item.supports_wpm`]="{ item }">
                  <v-checkbox v-model="item.supports_wpm" dense></v-checkbox>
                </template>
                <template v-slot:[`item.device_object_id`]="{ item }">
                  <v-text-field v-model="item.device_object_id" type="number" single-line style="width: 100px" dense />
