                <template v-slot:[`item.device_mac`]="{ item }">
                  <v-text-field v-model="item.device_mac" type="number" single-line style="width: 100px" dense />
                </template>
                <template v-slot:[`item.device_port`]="{ item }">
                  <v-text-field v-model="item.device_port" type="number" single-line style="width: 100px" dense />
                </template>
                <template v-slot:[`item.device_mask`]="{ item }">
                  <v-text-field v-model="item.device_mask" type="number" single-line style="width: 100px" dense />
                </template>
                <template v-slot:[`item.network_number`]="{ item }">
                  <v-text-field v-model="item.network_number" type="number" single-line style="width: 100px" dense />
                </template>
                <template v-slot:[`item.device_ip`]="{ item }">
                  <v-text-field v-model="item.device_ip" single-line style="width: 150px" dense />
                </template>
                <template v-slot:[`item.ethernet_mac_address`]="{ item }">
                  <v-text-field v-model="item.ethernet_mac_address" single-line style="width: 150px" dense />
                </template>
