      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <bm-mass-edit-dialog
      :is-open="dialogMassEdit"
      :on-close="() => (dialogMassEdit = false)"
      :edit-all="editAll"
      :edit-field="editField"
      :on-save="saveMassEdit"
    ></bm-mass-edit-dialog>

    <!-- who is dialog-->
    <v-dialog v-model="dialogWhoIs" width="800px">
      <v-card>
        <v-card-title>
          <span class="headline">Who Is</span>
        </v-card-title>
        <v-card-text class="text-start">
          <v-container>
            <v-row style="padding: 0 24px">
              <v-col cols="12" sm="6" md="4">
                <v-text-field
                  v-model="user_options.network_number"
                  label="Network number (if 0 will not look for networks)"
                  type="number"
                />
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="user_options.range_start" label="Search device id range from" type="number" />
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="user_options.range_end" label="Search device range id to" type="number" />
              </v-col>
              <v-col cols="12" sm="6" md="8">
                <v-checkbox
                  v-model="user_options.whois"
                  label="Optimised Search (if not ticked with scan for networks)"
                ></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-checkbox v-model="user_options.global_broadcast" label="Global broadcast"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="8">
                <v-checkbox v-model="user_options.is_mstp" label="Network Type is BACnet/MSTP"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-checkbox v-model="user_options.full_range" label="Full device id range"></v-checkbox>
              </v-col>
              <!-- <v-col cols="12" sm="12" md="12">
                <v-checkbox v-model="user_options.add_devices" label="ADD/SAVE DEVICES (this action will save all devices if ticked)"></v-checkbox>
              </v-col> -->
            </v-row>
            <v-expansion-panels :value="panel" multiple>
              <v-expansion-panel v-for="(key, index) in Object.keys(whoIsResult)" :key="index">
                <v-expansion-panel-header>
                  <h3>
                    {{ toUpperTextKey(key) }}
                    <v-divider inset vertical></v-divider>
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          x-small
                          class="mr-1 btn-height-22 white--text"
                          color="downloadColor"
                          v-bind="attrs"
                          v-on="on"
                          >Download<v-icon x-small right>mdi-download</v-icon></v-btn
                        >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(whoIsResult[key], 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(whoIsResult[key], 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(whoIsResult[key], 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                  </h3>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-data-table
                    :headers="key == 'discovered_devices' ? discoveredDeviceHeaders : addedDeviceHeaders"
                    :items="whoIsResult[key]"
                    class="elevation-1"
                    v-model="selectedAddDevices"
                    show-select
                    item-key="device_name"
                    :items-per-page="5"
                    dense
                  ></v-data-table>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="dialogWhoIs = false">Close</v-btn>
          <v-btn color="primary" text @click="saveWhoIs">Discover</v-btn>
          <v-btn
