    <!-- discover points dialog-->
    <bm-discover-points-dialog
      v-if="selected.length === 1"
      :is-open="dialogDiscoverPoints"
      :on-close="() => (dialogDiscoverPoints = false)"
      :device_uuid="selected[0].device_uuid"
      :on-save-add="createMutiPoints"
    ></bm-discover-points-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import BmMassEditDialog from './BmMassEditDialog';
import BmDiscoverPointsDialog from './BmDiscoverPointsDialog';
import BmDeviceFormDialog from './BmDeviceFormDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import {
  DEVICE_HEADERS,
  DISCOVER_DEVICE_HEADERS,
  ADDED_DEVICE_HEADERS,
  RULES,
  DEVICE_DEFAULT,
  DEVICE_EDIT_ALL_DEFAULT,
} from './bacnetMasterConstant';
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import { updatePostDeviceData, updatePostPointData } from './bacnetMasterUtils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    BmDiscoverPointsDialog,
    BmMassEditDialog,
    BmDeviceFormDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    rules: { ...RULES },
    tableProps: { ...DATA_TABLE_PROPS },
    editedItem: { ...DEVICE_DEFAULT },
    deviceHeaders: [...DEVICE_HEADERS],
    discoveredDeviceHeaders: [...DISCOVER_DEVICE_HEADERS],
    addedDeviceHeaders: [...ADDED_DEVICE_HEADERS],
    dialog: false,
    dialogDelete: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    dialogWhoIs: false,
    dialogDiscoverPoints: false,
    isClickSaveImport: false,
    valid: false,
    intervalId: null,
    editedIndex: -1,
    e1: 1,
    dataImport: '',
    action: '',
    search: '',
    searchItems: [],
    searchWizard: '',
    editField: '',
    selected: [],
    importArray: [],
    allPoints: [],
    dirs: [],
    panel: [],
    selectedAddDevices: [],
    network: {},
    editAll: {},
    whoIsResult: {},
    user_options: {},
    whoIsSaving: false,
    validWizardform: false,
    savingImport: false,
    tab: 0,
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    networksData() {
      return this.$store.state.bmNetworks;
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getData();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getData();
    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getData();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      try {
        this.loadingData = true;
        this.network = await axios.get(`bacnet_master/network/${this.$route.params.id}`);
        this.getDirs();
        this.editedItem.network_uuid = this.network.network_uuid;
        //update BmNetworksData
        this.updateBmNetworksData(this.network);
      } catch (error) {
      } finally {
        this.loadingData = false;
      }
    },
    getDirs() {
      this.dirs = [{ text: 'NETWORKS', link: '/bacnet-master/networks', disabled: false }];
      this.dirs.push({ text: `NET: ${this.network.network_name}`, disabled: true });
    },
    updateBmNetworksData(newNetwork) {
      const indexNetWork = this.networksData.findIndex(nw => nw.network_uuid == newNetwork.network_uuid);
      if (indexNetWork > -1) {
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeBmNetworksData', this.networksData);
      }
    },
    onClickOpen(item) {
      this.$router.push({ path: `/bacnet-master/devices/${item.device_uuid}/points` });
    },
    editItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    save() {
      if (this.editedIndex > -1) {
        this.updateDevice(this.editedItem);
      } else {
        this.createDevice(this.editedItem);
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.editedItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/device/${this.editedItem.device_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...DEVICE_DEFAULT, network_uuid: this.network.network_uuid });
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...DEVICE_DEFAULT, network_uuid: this.network.network_uuid });
        this.editedIndex = -1;
      });
    },
    async clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.network_uuid = this.network.network_uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try {
            await this.createMutiDevices(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {
          } finally {
            this.savingImport = false;
          }
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(device => {
        delete device.device_uuid;
        return device;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      if (!this.network.devices) return false;
      const value = item[fieldName];
      const devices = _cloneDeep(this.network.devices);
      if (this.editedIndex !== -1) {
        devices.splice(this.editedIndex, 1); //when edit
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    clickJsonExport(data) {
      const status = copyToClipboard(JSON.stringify(data));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertToExportData(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(array));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            device.points.forEach((point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                if (typeof point_value === 'object' && point_value !== null) {
                  delete point[point_key];
                } else {
                  const pointKey = `point[${indexP}].${point_key}`;
                  device = { ...device, [pointKey]: point_value };
                }
              });
            });
            delete device.points;
          } else {
            if (typeof value === 'object' && value !== null) delete device[key];
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      axios.patch(`bacnet_master/device/${data.device_uuid}`, data).then(response => {
        const index = this.network.devices.findIndex(d => d.device_uuid == response.device_uuid);
        Object.assign(this.network.devices[index], response);
        this.close();
        return this.$toasted.show('update success');
      });
    },
    async createDevice(data) {
      const points = data.points;
      const _data = updatePostDeviceData(data);
      const response = await axios.put(`bacnet_master/device`, _data);
      this.network.devices.push(response);
      if (this.importArray && this.importArray.length > 0) {
        const index = this.importArray.findIndex(n => n.device_name == response.device_name);
        if (index > -1) {
          Object.assign(this.importArray[index], response);
        }
      }
      this.close();
      this.$toasted.show('create success');
      if (points && points.length > 0) {
        this.createMutiPoints(points, response.device_uuid);
      }
    },
    async createPoint(data) {
      const _data = updatePostPointData(data);
      axios.put(`bacnet_master/point`, _data);
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...DEVICE_EDIT_ALL_DEFAULT };
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh() {
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    async saveWhoIs() {
      try {
        this.whoIsSaving = true;
        const result = await axios.post(
          `bacnet_master/b/network/whois/${this.network.network_uuid}`,
          this.user_options,
        );
        this.whoIsResult = {};
        Object.keys(result).forEach(key => {
          if (typeof result[key] === 'object' && result[key] !== null) {
            const devices = Object.keys(result[key]).map(subKey => {
              return result[key][subKey];
            });
            this.whoIsResult = { ...this.whoIsResult, [key]: devices };
          }
        });
        this.panel = Object.keys(this.whoIsResult).map((k, i) => i);
        this.$toasted.show('success!', { type: 'success' });
      } catch (error) {
        this.$toasted.show('fail!', { type: 'error' });
      } finally {
        this.whoIsSaving = false;
      }
    },
    openWhoIsDialog() {
      this.dialogWhoIs = true;
      this.whoIsResult = {};
      this.selectedAddDevices = [];
      this.user_options = {
        network_number: 0,
        is_mstp: false,
        whois: true,
        global_broadcast: false,
        full_range: true,
        range_start: 1,
        range_end: 202,
        add_devices: false,
        show_supported_services: false,
      };
    },
    async createMutiDevices(devices) {
      const promises = [];
      for (const item of devices) {
        item.network_uuid = this.network.network_uuid; //update to the current network_uuid
        if (!item.device_uuid) {
          promises.push(this.createDevice(item));
        }
      }
      await Promise.all(promises);
    },
    async createMutiPoints(points, devie_uuid) {
      const promises = [];
      for (const item of points) {
        item.device_uuid = devie_uuid ? devie_uuid : this.selected[0].device_uuid;
        promises.push(this.createPoint(item));
      }
      await Promise.all(promises);
      this.$toasted.show('create points success!', { type: 'success' });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
